import { deserialize as d, deserializeAs as das } from 'cerialize';

export class ErrorObject {
  /** A dot notation string that uniquely identifies the reason for the error or warning */
  @d code: string;

  /** The JSONPath to the source of the error. Omission of this indicates that the error is for the entire request. */
  @d source?: string;

  /** The expected value associated with the error code. */
  @d expects?: any;

  /** An English-only message relaying additional info for the purpose of clarity, troubleshooting, or other. */
  @d message?: string;

  /** Additional data related to the error. */
  @d details?: any;
}

/** Represents the error format of StandIn APIs. */
export class ErrorResponse {
  /** A UUID assigned for this error response */
  @d reference: string;

  /** Collections of errors. May be empty if no errors available for current response. */
  @das(ErrorObject) errors?: ErrorObject[];

  /** Collections of warnings. May be empty if no warnings available for current response. */
  @das(ErrorObject) warnings?: ErrorObject[];

  /** Rejected `Response` object for debug. */
  response: any;
}
