import { Injectable, Inject } from '@angular/core';
import { MessageService } from '@mastercard/ng-commons';

import { Observable } from 'rxjs/Observable';
import { filter } from 'rxjs/operators/filter';
import { map } from 'rxjs/operators/map';

import { MessageKeys } from './constants';

/**
 * Use this service to query/inform whether it's safe to navigate away without losing pending changes.
 * It also contains methods to open the confirmation dialog and get the user response.
 */
@Injectable()
export class PreventNavigationService {

  private storage: Storage;

  constructor(
    private messageService: MessageService,
    @Inject('Window') window: any,
  ) {
    this.storage = (window as Window).sessionStorage;
  }

  /** Starts listening to shell messages. Please call this in root component's init method. */
  start(): void {
    this.isSafeToQuit = true;
    this.messageService.get().pipe(
      filter(incoming => incoming.data.message === MessageKeys.CONFIRMATION_DIALOG_OPENED),
    ).subscribe(() => {
      this.isSafeToQuit = true;
    });
  }

  /** Flag to indicate whether it's safe to navigate away without losing pending changes. */
  get isSafeToQuit(): boolean {
    return this.storage.getItem('preventNavigation') === '0';
  }

  set isSafeToQuit(safe: boolean) {
    const prevent = !safe;
    this.storage.setItem('preventNavigation', String(+ prevent));
  }

  /** Opens the confirmation dialog. */
  showConfirmationDialog(): void {
    const message = MessageKeys.SHOW_CONFIRMATION_DIALOG;
    this.isSafeToQuit = true;
    this.messageService.post({ message });
  }

  /** Emits `true` when user confirms, `false` when user cancels. */
  confirmationDialogClosed(): Observable<boolean> {
    return this.messageService.get().pipe(
      filter(incoming => incoming.data.message === MessageKeys.CONFIRMATION_DIALOG_CLOSED),
      map(incoming => incoming.data.content.ok as boolean),
    );
  }

}
