import { Response } from '@angular/http';
import { ErrorResponse } from './error-response';

/** Wraps errors emitted by `HttpService`. */
export class HttpError extends Error {

  constructor(
    /** Actual error message */
    public message: string,

    /** Rejected response or the original error */
    public payload: ErrorResponse | Response | any,

    /** Translated, user-friendly error message */
    public displayMessage: string,

    /** HTTP status code of the response */
    public status?: number
  ) {
    super(message);
    this.name = 'HttpError';

    // otherwise this instanceof HttpError would be false o_O
    Object.setPrototypeOf(this, HttpError.prototype);
  }

}
