import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { filter, map, publishReplay, refCount } from 'rxjs/operators';

import { MessageService } from '@mastercard/ng-commons';
import { MessageKey } from './message-key';

export interface Action {
  code: string;
  hashPartsProperty?: string[];
  label?: string;
  labelToken?: string;
  order?: number;
  target?: number;
  targetArgs?: string;
  toolTipToken?: string;
  tooltip?: string;
  urlAndHashProperty?: string;
  urlProperty?: string;
}

/**
 * Service methods to query if the authenticated user is authorized to execute the given actions
 */
@Injectable()
export class PermissionsService {

  private stream: Observable<Action[]>;
  private initialized = false;

  constructor(
    readonly messageService: MessageService,
  ) {
    // empty
  }

  /**
   * Initializes the service by querying user's permissions.
   * It should be called at the initialization of the app's root component.
   */
  init(): void {
    if (this.initialized) throw new Error('Already initialized');
    this.initialized = true;

    this.stream = this.messageService.get().pipe(
      filter(me => me.data.message === MessageKey.GET_ACCESSIBLE_UI_ACTIONS_RESPONSE),
      map(me => me.data.content as Action[]),
      publishReplay(1), // caches the response
      refCount(), // unsubscribes when there's no more subscribers
    );
    this.messageService.post({ message: MessageKey.GET_ACCESSIBLE_UI_ACTIONS_QUERY });
  }

  /**
   * User's authorized actions.
   */
  get availableActions(): Observable<Action[]> {
    return this.stream;
  }

  /**
   * Checks if the authenticated user has access to the given action.
   */
  hasActionCode(actionCode: string): Observable<boolean> {
    return this.availableActions.pipe(map(
      (actions: Action[]) => actions.some(a => a.code === actionCode)
    ));
  }

  /**
   * Checks if the authenticated user has access to at least one of the given actions.
   */
  hasSome(...actionCodes: string[]): Observable<boolean> {
    return this.availableActions.pipe(map(
      (actions: Action[]) =>
        actionCodes.some(
          requested => actions.find(a => a.code === requested) !== undefined
        )
    ));
  }

  /**
   * Checks if the authenticated user has access to all of the given actions.
   */
  hasEvery(...actionCodes: string[]): Observable<boolean> {
    return this.availableActions.pipe(map(
      (actions: Action[]) =>
        actionCodes.every(
          requested => actions.find(a => a.code === requested) !== undefined
        )
    ));
  }

}
