import { CommonModule } from '@angular/common';
import { NgModule, ModuleWithProviders } from '@angular/core';
import {
  AccordionModule,
  AlertModule,
  ButtonsModule,
  CarouselModule,
  CollapseModule,
  ModalModule,
  BsDropdownModule,
  PaginationModule,
  ProgressbarModule,
  RatingModule,
  SortableModule,
  TabsModule,
  TimepickerModule,
  TooltipModule,
  TypeaheadModule,
  PopoverModule,
 } from 'ngx-bootstrap';
import { ToastrModule } from 'ngx-toastr';
import { MastercardCommons, LocaleService, Logger } from '@mastercard/ng-commons';
import { TranslateModule, TranslateService } from '@ngx-translate/core';

import { DatePickerComponent } from './date-picker/date-picker.component';
import { PermissionsService } from './permissions/permissions.service';
import { PleaseWaitComponent } from './please-wait/please-wait.component';
import { PleaseWaitService } from './please-wait/please-wait.service';
import { ToastrService } from './toastr/toastr.service';
import { CacheableHttpService } from './http/cacheable-http.service';
import { HttpService } from './http/http.service';
import { DownloadButtonComponent } from './download-button/download-button.component';
import { FcDialogComponent } from './fc-dialog/fc-dialog.component';
import { PreventNavigationService } from './prevent-navigation/prevent-navigation.service';
import { XSRFStrategy, CookieXSRFStrategy } from '@angular/http';

const bsModules = [
  AccordionModule,
  AlertModule,
  ButtonsModule,
  CarouselModule,
  CollapseModule,
  ModalModule,
  BsDropdownModule,
  PaginationModule,
  ProgressbarModule,
  RatingModule,
  SortableModule,
  TabsModule,
  TimepickerModule,
  TooltipModule,
  TypeaheadModule,
  PopoverModule,
];

export function httpFactory(
  http: CacheableHttpService,
  window: any,
  locale: LocaleService,
  logger: Logger,
  wait: PleaseWaitService,
  translate: TranslateService,
  toastr: ToastrService,
) {
  return new HttpService(http, window, locale, logger, wait, translate, toastr);
}

export function xsrfStrategyFactory() {
  return new CookieXSRFStrategy('TFC-XSRF-Token', 'X-TFC-XSRF-Token');
}

/** Collection of utilities for Fraud Center apps. */
@NgModule({
  imports: [
    CommonModule,
    MastercardCommons,
    ...bsModules,
    ToastrModule,
    TranslateModule,
  ],
  declarations: [
    PleaseWaitComponent,
    DatePickerComponent,
    DownloadButtonComponent,
    FcDialogComponent
  ],
  exports: [
    DatePickerComponent,
    PleaseWaitComponent,
    ToastrModule,
    DownloadButtonComponent,
    FcDialogComponent
  ],
})
export class FraudCenterCommons {

  /**
   * Use this method in your root module to register our providers.
   */
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: FraudCenterCommons,
      providers: [
        PermissionsService,
        PleaseWaitService,
        ToastrService,
        CacheableHttpService,
        PreventNavigationService,
        {
          provide: HttpService,
          useFactory: httpFactory,
          deps: [
            CacheableHttpService, 'Window', LocaleService, Logger, PleaseWaitService, TranslateService, ToastrService
          ],
        },
        {
          provide: XSRFStrategy,
          useFactory: xsrfStrategyFactory
        }
      ],
    };
  }

}
