import { Injectable, Inject } from '@angular/core';
import { Response, Headers, RequestOptions } from '@angular/http';
import { Serialize, Deserialize } from 'cerialize';
import { LocaleService, Logger } from '@mastercard/ng-commons';
import { TranslateService } from '@ngx-translate/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { _throw } from 'rxjs/observable/throw';
import { map, mergeMap, mergeMapTo, catchError, finalize, tap } from 'rxjs/operators';

import { PleaseWaitService } from '../please-wait/please-wait.service';
import { ToastrService } from '../toastr/toastr.service';
import { HttpReadRequest } from './http-read-request.class';
import { HttpWriteRequest } from './http-write-request.class';
import { HttpResponse } from './http-response.class';
import { HttpError } from './http-error.class';
import { HttpLogMessages, HttpDisplayMessages } from './constants';
import { ErrorResponse } from './error-response';

import { CacheableHttpService } from './cacheable-http.service';

/**
 * This service augments Angular's Http service with Mastercard's headers & error handling.
 *
 * It also adds a "please wait" message during the operation and toasts the result.
 *
 * All those options can be configured per request.
 *
 * The API is similar to Angular's Http class, but first you have to build a request object.
 * Use `HttpReadRequest` for GET & DELETE and `HttpWriteRequest` for POST, PUT & PATCH verbs.
 *
 * The service emits `HttpResponse` for successful requests and `HttpError` otherwise.
 */
@Injectable()
export class HttpService {

  private window: Window;
  private acceptLanguage: string;

  constructor(
    private delegate: CacheableHttpService,
    @Inject('Window') window: any,
    locale: LocaleService,
    private logger: Logger,
    private wait: PleaseWaitService,
    private translate: TranslateService,
    private toastr: ToastrService,
  ) {
    this.window = window;
    locale.getCurrent().subscribe(
      lang => {
        this.acceptLanguage = lang.replace('-', '_');
        this.logger.debug('Default HTTP request options', this.defaultOptions);
      },
    );
  }

  /**
   * Default HTTP headers that will be sent on each request.
   */
  get defaultHeaders(): Headers {
    return new Headers({
      'Accept': 'application/json',
      'Content-Type': 'application/json; charset=UTF-8',
      'X-MC-FS-Accept-Language': this.acceptLanguage,
      'X-PRDCT-GRP-ID': this.window.sessionStorage.getItem('prdctGrpId'),
      'X-Customer-ID': this.window.sessionStorage.getItem('customerId'),
    });
  }

  /**
   * Default `RequestOptions` that will be sent on each request.
   */
  get defaultOptions(): RequestOptions {
    return new RequestOptions({ headers: this.defaultHeaders });
  }

  /**
   * Performs a request with the `get` http method.
   *
   * Emits `HttpResponse` when successful, `HttpError` otherwise.
   * @param request the configuration object.
   */
  get<RP>(request: HttpReadRequest<RP>): Observable<HttpResponse<RP>> {
    const { url, expiresIn, forceRefresh } = request;
    const options = request.options || this.defaultOptions;
    const response$ = this.delegate.get(url, options, expiresIn, forceRefresh) as any;
    return this.handle(request, response$);
  }

  /**
   * Performs a request with the `post` http method.
   *
   * Emits `HttpResponse` when successful, `HttpError` otherwise.
   * @param request the configuration object.
   */
  post<RQ, RP>(request: HttpWriteRequest<RQ, RP>): Observable<HttpResponse<RP>> {
    const body = this.serialize(request);
    const response$ = this.delegate.post(request.url, body, request.options || this.defaultOptions);
    return this.handle(request, response$);
  }

  /**
   * Performs a request with the `put` http method.
   *
   * Emits `HttpResponse` when successful, `HttpError` otherwise.
   * @param request the configuration object.
   */
  put<RQ, RP>(request: HttpWriteRequest<RQ, RP>): Observable<HttpResponse<RP>> {
    const body = this.serialize(request);
    const response$ = this.delegate.put(request.url, body, request.options || this.defaultOptions);
    return this.handle(request, response$);
  }

  /**
   * Performs a request with the `delete` http method.
   *
   * Emits `HttpResponse` when successful, `HttpError` otherwise.
   * @param request the configuration object.
   */
  delete<RP>(request: HttpReadRequest<RP>): Observable<HttpResponse<RP>> {
    const response$ = this.delegate.delete(request.url, request.options || this.defaultOptions) as any;
    return this.handle(request, response$);
  }

  /**
   * Performs a request with the `patch` http method.
   *
   * Emits `HttpResponse` when successful, `HttpError` otherwise.
   * @param request the configuration object.
   */
  patch<RQ, RP>(request: HttpWriteRequest<RQ, RP>): Observable<HttpResponse<RP>> {
    const body = this.serialize(request);
    const response$ = this.delegate.patch(request.url, body, request.options || this.defaultOptions);
    return this.handle(request, response$);
  }

  private serialize<RQ, RP>(request: HttpWriteRequest<RQ, RP>): any {
    return request.requestType ? Serialize(request.body, request.requestType) : request.body;
  }

  private handle<RP>(request: HttpReadRequest<RP>, response$: Observable<Response>): Observable<HttpResponse<RP>> {
    let waitMessage: string;
    response$ = of(request.waitMessage).pipe(
      // maps to the translated wait msg,
      // or to false when there's no wait
      mergeMap(message => {
        if (typeof message === 'string') {
          return this.translate.get(message).pipe(
            tap(translated => {
              waitMessage = translated;
              this.logger.debug('adding wait:', translated);
              this.wait.add(translated);
            }),
          );
        }
        return of(false);
      }),
      // maps to the http response
      mergeMapTo(response$),
    );

    return this.handleResponseStatus(request, response$).pipe(
      map(response => this.deserialize(request, response)),
      catchError(error => this.handleError(request, error)),
      mergeMap(response => this.handleSuccess(request, response)),
      finalize(() => {
        if (waitMessage) {
          this.logger.debug('removing wait:', waitMessage);
          this.wait.remove(waitMessage);
        }
      }),
    );
  }

  private handleResponseStatus<RP>(request: HttpReadRequest<RP>, response$: Observable<Response>) {
    const errorMsg = request.toastTokens.ERROR || HttpDisplayMessages.ERROR;
    const handle = (response: Response) => {
      if (!(response instanceof Response)) {
        const httpError = new HttpError(HttpLogMessages.NOT_A_RESPONSE, response, errorMsg);
        return _throw(httpError);
      }

      const status = response.status;
      const found = request.responseStatus.some(s => s === status);
      if (found) return of(response); // success

      // failure
      const wrongStatusMessage = request.toastTokens[status] || request.toastTokens.ERROR
        || HttpDisplayMessages[status] || HttpDisplayMessages.ERROR;
      let payload;
      try {
        payload = Deserialize(response.json(), ErrorResponse);
        payload.response = response;
        if (!payload.reference) {
          payload = response;
        }
      } catch (e) {
        payload = response;
      }

      const error = new HttpError(HttpLogMessages.WRONG_STATUS, payload, wrongStatusMessage, status);
      return _throw(error);
    };
    return response$.pipe(catchError(handle), mergeMap(handle));
  }

  private deserialize<RP>(request: HttpReadRequest<RP>, response: Response): HttpResponse<RP> {
    if (request.responseType) {
      const data = Deserialize(response.json(), request.responseType);
      return new HttpResponse<RP>(response, data);
    }
    return new HttpResponse<RP>(response);
  }

  private handleError<RP>(request: HttpReadRequest<RP>, error: any): Observable<any> {
    let result: HttpError;
    const token = request.toastTokens.ERROR || HttpDisplayMessages.ERROR;

    if (error instanceof HttpError) {
      result = error;
    } else {
      result = new HttpError(HttpLogMessages.UNEXPECTED, error, token);
    }

    return this.translate.get(result.displayMessage).pipe(
      mergeMap(translated => {
        this.logger.debug('toasting error:', request.toastOnFailure, translated);
        if (request.toastOnFailure) this.toastr.error(translated);
        return _throw(result);
      }),
    );
  }

  private handleSuccess<RP>(request: HttpReadRequest<RP>, response: HttpResponse<RP>): Observable<HttpResponse<RP>> {
    const token = request.toastTokens.OK || HttpDisplayMessages.OK;
    this.logger.debug('toasting success:', request.toastOnSuccess, token);
    if (request.toastOnSuccess) {
      return this.translate.get(token).pipe(
        map(translated => {
          this.toastr.success(translated);
          return response;
        }),
      );
    }
    return of(response);
  }

}
